import { useEffect, useState } from "react"
import { motion, AnimatePresence } from "framer-motion"
import { Download, QrCode, X, Copy } from "lucide-react"
import { QRCodeCanvas } from 'qrcode.react';
import { toast } from "react-toastify"

export default function FilesPage() {
  const [files, setFiles] = useState([])
  const [loading, setLoading] = useState(true)
  const [qrFile, setQrFile] = useState(null) // file selected for QR popup

  useEffect(() => {
    const fetchFiles = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/files/list/all")
        const data = await res.json()
        setFiles(data)
      } catch (err) {
        console.error("Error fetching files:", err)
      } finally {
        setLoading(false)
      }
    }
    fetchFiles()
  }, [])

  const handleCopy = (url) => {
    navigator.clipboard.writeText(url)
    toast.success("Link copied to clipboard")
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 p-6">
      <h1 className="text-2xl font-bold mb-6 bg-gradient-to-r from-blue-600 to-indigo-600 bg-clip-text text-transparent">
        Your Files
      </h1>

      {loading ? (
        <p className="text-slate-500">Loading files...</p>
      ) : files.length === 0 ? (
        <p className="text-slate-500">No files uploaded yet.</p>
      ) : (
        <div className="overflow-x-auto bg-white rounded-2xl shadow">
          <table className="w-full border-collapse">
            <thead>
              <tr className="bg-slate-100 text-left text-sm text-slate-600">
                <th className="p-4">Filename</th>
                <th className="p-4">Size</th>
                <th className="p-4">Uploaded</th>
                <th className="p-4">Actions</th>
              </tr>
            </thead>
            <tbody>
              {files.map((file, i) => {
                const url = `http://localhost:5000/api/files/${file._id}`
                return (
                  <motion.tr
                    key={file._id}
                    className="border-b hover:bg-slate-50 text-sm"
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: i * 0.05 }}
                  >
                    <td className="p-4 font-medium">{file.filename}</td>
                    <td className="p-4">
                      {formatFileSize(file.length || file.size || 0)}
                    </td>
                    <td className="p-4">
                      {new Date(file.uploadDate).toLocaleString()}
                    </td>
                    <td className="p-4 flex gap-2">
                      {/* Download Button */}
                      <a
                        href={url}
                        className="flex items-center gap-1 px-3 py-1.5 bg-blue-600 hover:bg-blue-700 text-white rounded-lg shadow text-xs transition"
                      >
                        <Download className="w-4 h-4" /> Download
                      </a>

                      {/* QR Code Button */}
                      <button
                        onClick={() => setQrFile(file)}
                        className="flex items-center gap-1 px-3 py-1.5 bg-slate-700 hover:bg-slate-800 text-white rounded-lg shadow text-xs transition"
                      >
                        <QrCode className="w-4 h-4" /> QR
                      </button>
                    </td>
                  </motion.tr>
                )
              })}
            </tbody>
          </table>
        </div>
      )}

      {/* QR Modal */}
      <AnimatePresence>
        {qrFile && (
          <motion.div
            className="fixed inset-0 bg-black/50 flex items-center justify-center z-50"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <motion.div
              className="bg-white rounded-2xl shadow-lg p-6 w-[350px] text-center relative"
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.8, opacity: 0 }}
            >
              {/* Close Button */}
              <button
                onClick={() => setQrFile(null)}
                className="absolute top-3 right-3 text-slate-500 hover:text-slate-700"
              >
                <X className="w-5 h-5" />
              </button>

              <h2 className="text-lg font-semibold mb-4">Scan or Copy Link</h2>

              {/* QR Code */}
              <QRCodeCanvas
                value={`http://localhost:5000/api/files/${qrFile._id}`}
                size={200}
                className="mx-auto"
              />

              {/* Link + Copy Button */}
              <div className="mt-4 bg-slate-100 rounded-lg p-2 flex items-center justify-between gap-2 text-sm">
                <span className="truncate text-blue-600">
                  http://localhost:5000/api/files/{qrFile._id}
                </span>
                <button
                  onClick={() =>
                    handleCopy(`http://localhost:5000/api/files/${qrFile._id}`)
                  }
                  className="flex items-center gap-1 px-2 py-1 bg-blue-600 hover:bg-blue-700 text-white rounded-md text-xs"
                >
                  <Copy className="w-4 h-4" /> Copy
                </button>
              </div>

              <p className="mt-3 text-sm text-slate-500 break-all">
                {qrFile.filename}
              </p>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  )
}

function formatFileSize(bytes) {
  if (bytes === 0) return "0 B"
  const sizes = ["B", "KB", "MB", "GB", "TB"]
  const i = Math.floor(Math.log(bytes) / Math.log(1024))
  return (bytes / Math.pow(1024, i)).toFixed(1) + " " + sizes[i]
}
